.day4-contanier {
    width: 100%;
    min-height: 1000px;
    height: 100%;
    background-image: url("../../assets/images/D4.jpg");
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.day4-section {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(4px);
    color: #1b27c7;
    padding: 8px 8px;
    text-align: center;
}


.day4-section h2 {
    font-size: 70px;
    font-weight: 900;
    text-decoration: underline;
}

.day4-section h3 {
    font-size: 40px;
    font-weight: 900;
}

