.day5-contanier {
    width: 100%;
    min-height: 1000px;
    height: 100%;
    background-image: url("../../assets/images/D5.jpg");
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.day5-section {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(4px);
    color: #e2f24c;
    padding: 8px 8px;
    text-align: center;
}


.day5-section h2 {
    font-size: 70px;
    font-weight: 900;
    text-decoration: underline;
}

.day5-section h3 {
    font-size: 40px;
    font-weight: 900;
}